.todo-list-img{
    position: relative;
    width: 90%;
    margin: auto;
    text-align: center;
}
.todo-list-img h3{
    padding: 0;
    margin: 0;
}
.todo-list-img img{
    width: 65%;
}

@media screen and (max-width:600px){
    .todo-list-img img{
        width: 90%;
    }
    
}